* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.country-list {
  list-style: none;
}

.country-list__item {
  display: flex;
}

.country-list__item:not(:last-child) {
  margin-bottom: 10px;
}

.country-list__meta {
  margin: 0;

  font-weight: 700;
  line-height: 2.1;
}

.country-list__img {
  margin-right: 10px;
  border-radius: 4px;
  box-shadow: 7px 7px 19px -3px rgba(0, 0, 0, 0.6);
}

.country-card__img {
  border-radius: 18px;
  box-shadow: 7px 7px 19px -3px rgba(0, 0, 0, 0.6);
}
